var utils = require('./utils');
var async = require('async');
var vue = require('vue');
var user = JSON.parse(localStorage.getItem('user'));
var $ = null;
require('../css/index.css');
require.ensure(['jquery'], function() {
    $ = require('jquery');
    utils.jssdk($);
}, 'jquery');

var voice = new vue({
    el: '#voice',
    data: {
        user: user
    },
    methods: {
        //
        startRecord: function(ev) {
            ev.preventDefault();
            if (!user.record) {
                setInterval(function() {
                    user.time--
                }, 1000);
                wx.startRecord();
                user.record = true;
                //
                wx.onVoiceRecordEnd({
                    //  complete 
                    complete: function(res) {
                        user.localId = res.localId;
                    }
                });
            } else {
                this.stopRecord();
            }

        },
        //
        stopRecord: function() {
            wx.stopRecord({
                success: function(res) {
                    user.localId = res.localId;
                    user.record = false;
                }
            });
        },
        //
        playVoice: function() {
            wx.playVoice({
                localId: user.localId // IDstopRecord
            });
            // 
            wx.onVoicePlayEnd({
                success: function(res) {
                    user.localId = res.localId; // ID
                }
            });
        },
        //
        pauseVoice: function() {
            wx.pauseVoice({
                localId: user.localId // IDstopRecord
            });
        },
        //
        stopVoice: function() {
            wx.stopVoice({
                localId: user.localId // IDstopRecord
            });
        },
        //
        uploadVoice: function() {
            async.waterfall([
                function(cb) {
                    wx.uploadVoice({
                        localId: user.localId, // IDstopRecord
                        isShowProgressTips: 1, // 1
                        success: function(res) {
                            user.serverId = res.serverId; // ID
                            cb(null);
                        },
                        error: function(err) {
                            cb(new Error(err));
                        }
                    });
                },
                function(cb) {
                    $.ajax({
                        url: '/wechat/voice/save',
                        type: "POST",
                        data: user,
                        success: function(data) {
                            //do st.
                            console.log(656565);

                        },
                        error: function(err) {
                            cb(new Error(err));
                        }
                    });
                },
            ], function(err, result) {
                if (err) {
                    console.log(err);
                } else {
                    //do st.
                    console.log('');
                }
            });

        },
        //

    }
});



